import React, { useContext, useEffect, useState } from "react";
import { StoreContext } from "../../store/StoreProvider";
import ItemsList from "./subcomponents/ItemsList";
import ItemsShowList from "./subcomponents/ItemsShowList";
import WebMap from "./subcomponents/WebMap";

export const itemsPerPage = 6;

const Shop = () => {
  const { items } = useContext(StoreContext);
  const [category, setCategory] = useState("all");
  const [itemsArray, setItemsArray] = useState("");

  useEffect(() => {
    const correctItems = [];
    items &&
      items.filter((i) => {
        if (i.category === category || category === "all") {
          correctItems.push(i);
        }
      });
    setItemsArray(correctItems);
  }, [category]);

  return (
    <div className="container">
      <WebMap category={category} setCategory={setCategory} />
      <main className="row">
        <ItemsList setCategory={setCategory} />
        <ItemsShowList category={category} itemsArray={itemsArray} />
      </main>
    </div>
  );
};

export default Shop;
